*{
  margin: 0;
}
.app {
  background-color: #fafafa;
  height: 100vh;
}

.app__header {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
}

.app__signup {
  display: flex;
  flex-direction: column;
  
  }

  .app__headerImage{
    object-fit: contain;
    width: 100%;
    height: 60px;
  }

  .app__headerImage1{
    object-fit: contain;
    width: 60px;
    height: 60px;
  }

  .app__posts{
    padding: 20px;
    display: flex;
    justify-content: center;
  }
  
  .app__postsRight{
  margin-left: 20px;
  }